import React, { useState, useEffect, useRef, forwardRef, useImperativeHandle } from "react";
import Lottie, { useLottie } from "lottie-react";

// Import your JSON animations
import stemJson from "./../Assets/stem.json";
import leafJson from "./../Assets/leaf.json";
import flowerJson from "./../Assets/flower.json";
import frontLeafJson from "./../Assets/front-leaf.json";
import deadLeafJson from "./../Assets/brown-leaf.json";

import "./../Assets/PlantAnimation.css";

const PlantAnimation = forwardRef((props, ref) => {
  const stemRef = useRef(null);
  const { onStepChange, onRemoveDeadLeaves } = props;

  const [leaves, setLeaves] = useState([]);
  const [flowers, setFlowers] = useState([]);
  const [frontLeaves, setFrontLeaves] = useState([]);
  const [removedLeaves, setRemovedLeaves] = useState([]);

  const [frames, setFrames] = useState([]);
  const [currentFrame, setCurrentFrame] = useState(0);

  const [stepIndex, setStepIndex] = useState(0);
  const segments = [100, 110, 120, 130, 140];

  const roundToNearestTen = (num) => {
    return Math.round(num / 10) * 10;
  };

  const {
    View: StemView,
    playSegments,
    animation,
    setDirection,
    setSpeed,
    play,
    pause
  } = useLottie({
    animationData: stemJson,
    loop: false,
    autoplay: false,
    lottieRef: stemRef,
    onEnterFrame: (e) => {
      setCurrentFrame(e.currentTime);
    }
  });

  useImperativeHandle(ref, () => ({
    growNextStep: handleNextGrowth,
    alreadyGrowth: handleAlreadyGrowth,
    randomizeLeaves: () => randomize(leaves, setLeaves, "falling"),
    randomizeFlowers: () => randomize(flowers, setFlowers, "falling"),
    randomizeFrontLeaves: () => randomize(frontLeaves, setFrontLeaves, "falling"),

    randomizeDeadLeaves: () => randomize(leaves, setLeaves, "dead"),
    resetDeadWithFallingLeaves: () => handleResetDeadAndFallingLeaves()
  }));

  useEffect(() => {
    console.log('leaves', leaves);
  }, [leaves]);

  const growPlant = () => {
    playSegments([0, 100], true);
  };

  // const addLeaf = (idx) => {
  //   setLeaves((prev) => {
  //     if (prev.some((leaf) => leaf.key === idx)) return prev;
  //     return [...prev, { key: idx, dead: false, falling: false }];
  //   });
  // };

  const addLeaf = (idx) => {
    setLeaves((prev) => {
      if (prev.some((leaf) => leaf.key === idx)) return prev;

      const leafTemplate = leafTemplates.find((template) => template.id === idx);

      if (leafTemplate) {
        return [
          ...prev,
          { ...leafTemplate, key: idx, dead: false, falling: false }
        ];
      }
      return prev;
    });
  };

  const addFlower = (idx) => {
    setFlowers((prev) => {
      if (prev.some((flower) => flower.key === idx)) return prev;
      return [...prev, { key: idx, dead: false, falling: false }];
    });
  };

  const addFrontLeaf = (idx) => {
    setFrontLeaves((prev) => {
      if (prev.some((leaf) => leaf.key === idx)) return prev;
      return [...prev, { key: idx, dead: false, falling: false }];
    });
  };

  const randomize = (items, setItems, type) => {
    const candidates = items.filter((i) => !i[type]);
    if (!candidates.length) return;

    const count = Math.min(candidates.length, Math.floor(Math.random() * 2) + 1);
    const selected = candidates.slice(0, count);

    setItems((prev) =>
      prev.map((i) =>
        selected.includes(i) ? { ...i, [type]: true } : i
      )
    );

    if (type === "falling") {
      selected.forEach((item) => {
        setTimeout(() => {
          setItems((prev) => prev.filter((i) => i.key !== item.key));
        }, (Math.random() * 3 + 2) * 1000);
      });
    }

  };

  const handleAlreadyGrowth = (targetStepIndex = stepIndex) => {
    setStepIndex(targetStepIndex);

  //   if (targetStepIndex >= steps.length) return;

  //   for (let i = stepIndex; i <= targetStepIndex; i++) {
  //     const step = steps[i];
      
  //     if (!step) {
  //       console.warn(`Step at index ${i} is undefined`);
  //       continue;
  //     }
      
  //     const { leaves = [], flowers = [], frontLeaves = [] } = step;

  //     leaves.forEach(addLeaf);
  //     flowers.forEach(addFlower);
  //     frontLeaves.forEach(addFrontLeaf);

  //     const segmentStart = i === 0 ? 0 : segments[i - 1];
  //     const segmentEnd = segments[i];

  //     if (segmentEnd !== undefined) playSegments([segmentStart, segmentEnd], true);
  //   }

  //   if (targetStepIndex < steps.length - 1) {
  //     // setStepIndex(targetStepIndex + 1);
  //     setStepIndex(targetStepIndex);
  //   }
  };

  const handleNextGrowth = () => {
    setStepIndex(prevStepIndex => {
      if (prevStepIndex >= steps.length - 1) {
        console.log("Already at last step, not incrementing");
        return prevStepIndex;
      }

      return prevStepIndex + 1;
    });
  };

  // const handleRemoveDeadLeaves = (leaf) => {
  //   setLeaves((prevLeaves) => {
  //     return prevLeaves.map((l) =>
  //       l.key === leaf.key
  //         ? { ...l, falling: l.dead === true ? true : l.falling }
  //         : l
  //     );
  //   });
  // };

  const handleRemoveDeadLeaves = (leaf) => {
    console.log("Removing leaf:", leaf);

    // if (!leaf || !leaf.key) {
    if (!leaf) {
      console.error("Invalid leaf object", leaf);
      return;
    }

    setLeaves((prevLeaves) => {
      const updatedLeaves = prevLeaves.map((l) => {
        if (l.key === leaf.key && l.dead) {
          const updatedLeaf = { ...l, falling: true, dead: false };

          setTimeout(() => {
            setLeaves((prev) => prev.filter((item) => item.key !== leaf.key));

            setRemovedLeaves((prevRemoved) => {
              if (!prevRemoved.includes(leaf.key)) {
                return [...prevRemoved, leaf.key];
              }
              return prevRemoved;
            });
          }, 3000);

          return updatedLeaf;
        }
        return l;
      });

      return updatedLeaves;
    });
  };


  const handleResetDeadAndFallingLeaves = () => {
    removedLeaves.forEach((leafKey) => {
      const leafTemplate = leafTemplates.find((template) => template.id === leafKey);
      if (leafTemplate) {
        addLeaf(leafKey);
      }
    });

    setRemovedLeaves([]);

    // setLeaves((prevLeaves) =>
    //   prevLeaves.map((leaf) => ({
    //     ...leaf,
        
    //     dead: leaf.falling ? false : leaf.dead,      
    //     falling: leaf.falling ? false : leaf.falling,   
    //   }))
    // );
  };

  
  useEffect(() => {
    console.log('leaves',leaves)
  }, [leaves]);

  useEffect(() => {
    if (stepIndex < 0 || stepIndex >= steps.length) return;

    for (let i = 0; i <= stepIndex; i++) {
      const step = steps[i];
      
      if (!step) {
        console.warn(`Step at index ${i} is undefined`);
        continue;
      }
      
      const { leaves = [], flowers = [], frontLeaves = [], deadLeaves = [] } = step;

      leaves.forEach(addLeaf);
      flowers.forEach(addFlower);
      frontLeaves.forEach(addFrontLeaf);

      const segmentStart = i === 0 ? 0 : segments[i - 1];
      const segmentEnd = segments[i];

      if (segmentEnd !== undefined) playSegments([segmentStart, segmentEnd], true);
    }

    if (onStepChange) {
      onStepChange(stepIndex);
    }
  }, [stepIndex]);

  // const step = steps[stepIndex];
    // if (!step) return;

    // const { leaves = [], flowers = [], frontLeaves = [] } = step;
    // leaves.forEach(addLeaf);
    // flowers.forEach(addFlower);
    // frontLeaves.forEach(addFrontLeaf);

    // const segmentStart = stepIndex === 0 ? 0 : segments[stepIndex - 1];
    // const segmentEnd = segments[stepIndex];

    // if (segmentEnd !== undefined) {
    //   playSegments([segmentStart, segmentEnd], true);
    // }

  // const handleNextGrowth = () => {
  //   setStepIndex(prevStepIndex => {
  //     if (prevStepIndex >= steps.length - 1) {
  //       console.log("Already at last step, not incrementing");
  //       return prevStepIndex;
  //     }

  //     // console.log('stepIndex', prevStepIndex);

  //     const step = steps[prevStepIndex];
  //     if (!step) return prevStepIndex;

  //     const { leaves = [], flowers = [], frontLeaves = [] } = step;
  //     leaves.forEach(addLeaf);
  //     flowers.forEach(addFlower);
  //     frontLeaves.forEach(addFrontLeaf);

  //     const segmentStart = prevStepIndex === 0 ? 0 : segments[prevStepIndex - 1];
  //     const segmentEnd = segments[prevStepIndex];

  //     if (segmentEnd !== undefined) {
  //       playSegments([segmentStart, segmentEnd], true);
  //     }

  //     const nextStepIndex = prevStepIndex + 1;

  //     // console.log('nextStepIndex', nextStepIndex);

  //     return nextStepIndex;
  //   });
  // };

  useEffect(() => {
    let lastFrame = frames[frames.length - 1];
    if(lastFrame === undefined) lastFrame = 0;

    const orignalFrame = roundToNearestTen(currentFrame + lastFrame);

    if(segments?.length > 0 ) {
      segments.forEach((segment, index) => {
        if ( orignalFrame === segment ) {
          setFrames((prevFrames) => {
            if (!prevFrames.includes(Math.round(orignalFrame)) && Math.round(orignalFrame) > 0) {
              return [...prevFrames, Math.round(orignalFrame)];
            }

            return prevFrames;
          });
        }
      });
    }
  }, [currentFrame]);

  useEffect(() => {
    const findDeadLeaves = leaves?.filter( x => x.dead === true);

    if(leaves.length !== 0) onRemoveDeadLeaves(findDeadLeaves?.length);
  }, [removedLeaves]);

  return (
    <div className="lottie-container">
      <div id="lottie-wrapper" style={{ right: "130px" }}>
        <div className="lottie-layer">{StemView}</div>
        
        <div className="lottie-layer">
          {
            leaves.map((leaf, key) => (
              <div
                key={leaf.key}
                className={`leaf ${leaf.dead ? "dead-leaf" : ""} ${leaf.falling ? "falling-leaf" : ""}`}

                // style={{
                //   ...leafTemplates[leaf.key], 
                //   zIndex: ((leaves.length - key) + 100),             
                // }}

                style={{ 
                  width: leaf.width, 
                  height: leaf.height, 
                  left: leaf.left, 
                  top: leaf.top, 
                  transform: leaf.transform, 
                  zIndex: ((leaves.length - key) + 100) 
                }}
                onClick={() => handleRemoveDeadLeaves(leaf)}
              >
                <Lottie animationData={leafJson} loop={false} autoplay />
              </div>
            ))
          }
        </div>

        <div className="lottie-layer">
          {flowers.map((flower) => (
            <div
              key={flower.key}
              className={`flower ${flower.dead ? "dead-flower" : ""} ${
                flower.falling ? "falling-flower" : ""
              }`}
              style={flowerTemplates[flower.key]}
            >
              <Lottie animationData={flowerJson} loop={false} autoplay />
            </div>
          ))}
        </div>

        <div className="lottie-layer">
          {frontLeaves.map((leaf) => (
            <div
              key={leaf.key}
              className={`leaf ${leaf.dead ? "dead-leaf" : ""} ${leaf.falling ? "falling-leaf" : ""}`}
              style={frontLeafTemplates[leaf.key]}
            >
              <Lottie animationData={frontLeafJson} loop={false} autoplay />
            </div>
          ))}
        </div>
        
      </div>
    </div>
  );
});

const steps = [
  {
    leaves: [],
    flowers: [],
    frontLeaves: [],
    deadLeaves: [0]
  },
  {
    leaves: [0, 1],
    flowers: [0],
    frontLeaves: [],
    deadLeaves: []
  },
  {
    leaves: [2, 3],
    flowers: [1],
    frontLeaves: [],
    deadLeaves: []
  },
  {
    leaves: [],
    flowers: [2],
    frontLeaves: [0],
    deadLeaves: []
  },
  {
    leaves: [6, 7],
    flowers: [3],
    frontLeaves: [],
    deadLeaves: []
  },
];

const leafTemplates = [
  { id: 0, width: 100, height: 150, left: 305, top: 360, transform: "rotate(-15deg) scaleX(-1)" },
  { id: 1, width: 100, height: 150, left: 375, top: 340, transform: "rotate(15deg) scale(1)" },

  { id: 2, width: 100, height: 150, left: 300, top: 320, transform: "scaleX(-1)" },
  { id: 3, width: 100, height: 150, left: 375, top: 295, transform: " scale(1)" },

  { id: 4, width: 80, height: 150, left: 320, top: 275, transform: "scaleX(-1)" },
  { id: 5, width: 80, height: 150, left: 375, top: 260, transform: "rotate(10deg) scale(1)" },

  { id: 6, width: 70, height: 70, left: 335, top: 235, transform: "scaleX(-1)" },
  { id: 7, width: 70, height: 70, left: 385, top: 220, transform: "rotate(30deg) scale(1)" }
];

const flowerTemplates = [
  { width: 300, height: 300, left: 230, top: 315, transform: 'rotate(10deg)', className: 'flower' },
  { width: 300, height: 300, left: 220, top: 190, transform: 'rotate(22deg) scaleX(-1)', className: 'flower' },
  { width: 300, height: 300, left: 245, top: 165, transform: '', className: 'flower' },
  { width: 300, height: 300, left: 265, top: 100, transform: 'rotate(-20deg)', className: 'flower' }
];

const frontLeafTemplates = [
  { width: 230, height: 150, left: 275, top: 240, transform: "" }
];

export default PlantAnimation;